.container {
  height: 100%;
  display: flex;
  justify-content: center;
}

.movies {
  display: grid;
  grid-template-columns: repeat(2, minmax(400px, 1fr));
  grid-gap: 100px;
  padding: 50px;
  width: 100%;
  padding-top: 70px;
}
.loader {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
